import { AbstractControl, ValidationErrors, ValidatorFn, Validators } from '@angular/forms';

export function patternWithMessage(regex: RegExp, message: string): ValidatorFn {
  return (control: AbstractControl): ValidationErrors | null => {
    const patternValidator = Validators.pattern(regex); // Get the original pattern validator
    const error = patternValidator(control); // Check if the original validator fails

    if (error) {
      // If it fails, return *your* custom error object
      // Using a key like 'customPattern' to avoid collision with the default 'pattern'
      return { customPattern: message };
    }

    return null; // No error
  };
}
